.nav {
    background-color: #52a8ec;
    border-radius: 20px;
    height: 410px;
    min-width: 20%;
    display: flex;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
}

.container li {
    font-size: 16pt;
}

a {
    text-decoration: none;
}

.activeLink {
    color: #e9aa09;
}