* {
    margin: 0;
    padding: 0;
}
body{
    background-color: #fafafb;
}

.generalContainer {
    display: flex;
    flex-direction: column;
    min-width: 100%;
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 100px;
}

.centerContainer{
    display: flex;
    flex-direction: column;
    width: 50%;
}

ul {
    list-style-type: none;
}






