.container{
    min-width: 100%;
    height: 400px;
}

.BGPhotoContainer{
    width: 100%;
    height: 250px;
}
.BGPhoto{
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;
}

.PhotoContainer{
    width: 150px;
    height: 150px;
    position: relative;
}
.Photo{
    position: absolute;
    bottom:70px;
    left: 360px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 80px;
}